@import '../../../../Global/ScssPartials/systemColorScheme';
@import '../../../../Global/ScssPartials/utilities';
.PatientCard {
  @extend .frameGradient;
  min-height: 10em;
}
.CardContent {
  background-color: var(--frameColor);
  padding: 0.5em;
  border-radius: 20px;
  @include flex-center(column, center, initial, 1em);
  height: 100%;
}
.patientContainer {
  * {
    user-select: none;
  }
  display: flex;
  flex-direction: column;
  gap: 8px !important;
